body {
  margin: 0;
  padding: 0;
  font-family: Arial;
  font-weight: bold;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
  width: 460px;
  margin-block: 30px;
  margin-inline: auto;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    width: 920px;
  }
}

.people .person {
  transition-property: background-color, border-radius, transform;
  transition-duration: .01ms, .01ms, 1s;
}

.people .person .photo {
  text-align: center;
  position: relative;
}

.people .person .photo .rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 32px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 85px;
  right: 15px;
}

.people .person .photo > img {
  border-radius: 200px;
  width: 200px;
  transition-duration: 1s;
}

.people .person .details {
  text-align: center;
  font-weight: 500;
}

.people .person .details .name {
  color: #2f4f4f;
  font-size: 20px;
  line-height: 23px;
}

.people .person .details .extra {
  visibility: hidden;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 18px;
  line-height: 20.7px;
}

.people .person:hover {
  z-index: 999;
  background-color: #eee;
  border-radius: 10px;
  transition-property: background-color, border-radius, transform;
  transition-duration: .01ms, .01ms, 1s;
  transform: scale(1.2);
}

.people .person:hover .extra {
  visibility: visible;
}

.people .person:hover .photo > img {
  border-radius: 50px;
  transition-duration: 1s;
}
